.accordion {
    &__item {
        padding: 29px 30px 28px;
        border: 1px solid var(--Gainsboro);
        cursor: pointer;
        transition: all 0.4s;
        &:not(:last-child) {
            margin-bottom: 10px;
        }
        &:last-child {
            margin-bottom: 60px;
        }
    }
    &__hovered {
        box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.15);
        .accordion__title {
            padding-bottom: 25px;
            border-bottom: 1px dashed var(--Gainsboro);
            &:before {
                content: "â€“";
                margin-top: -7px;
            }
            span {
                margin-top: 1px;
            }
        }
        .accordion__body {
            margin-top: 18px;
            overflow: visible;
            height: auto;
        }
    }
    &__title {
        display: flex;
        align-items: flex-start;
        transition: all 0.2s;
        &>div {
            flex-shrink: 1;
        }
        h3 {
            display: inline;
            margin: 0 30px 0 0;
            font-size: 13px;
            text-transform: uppercase;
            line-height: 1.3;
        }
        span {
            display: inline-block;
            font-size: 13px;
            line-height: 1.3;
        }
        &:before {
            content: "+";
            display: inline-block;
            font-size: 25px;
            font-family: "OpenSans";
            font-weight: 700;
            margin: -5px 30px 0 0;
        }
    }
    &__body {
        height: 0;
        overflow: hidden;
        font-size: 13px;
        line-height: 19px;
        transition: all 0.6s;
        h5 {
            margin: 0 0 18px;
            font-size: 13px;
            line-height: 1.2;
        }
        ul {
            margin: 0;
            padding: 0;
            list-style: none;
            margin-bottom: 30px;
            li:not(:last-child) {
                margin-bottom: 10px;
            }
            li {
                display: flex;
                align-items: flex-start;
                &:before {
                    content: "";
                    display: block;
                    height: 5px;
                    width: 5px;
                    border-radius: 50%;
                    background-color: var(--mainColor);
                    margin: 7.5px 9px 0 0;
                    flex-shrink: 0;
                }
                span {
                    display: block;
                    flex-shrink: 1;
                    line-height: 1.4;
                }
            }
        }
        button {
            margin-top: 5px;
            cursor: pointer;
            &:focus {
                outline: none;
            }
        }
        p {
            margin: 20px 0 0;
        }
    }
}
.faq h3 {
    font-weight: 600;
}
@media(max-width: 767px) {
    .accordion {
        &__item {
            &:last-child {
                margin-bottom: 30px;
            }
        }
        &__body {
            button {
                margin-top: 0;
            }
            ul {
                margin-bottom: 26px;
                li:not(:last-child) {
                    margin-bottom: 8px;
                }
            }
        }
        &__hovered .accordion__title {
            padding-bottom: 25px;
        }
    }
}
@media(max-width: 575px) {
    .accordion {
        &__item {
            padding: 18px 20px;
        }
        &__hovered .accordion__title:before {
            margin-top: -5px;
        }
        &__title {
            h3 {
                display: block;
                margin-right: 0;
                font-weight: 600;
                font-size: 15px;
            }
            &:before {
                font-size: 24px;
                margin: -3px 17px 0 0;
            }
        }
        &__body {
            ul {
                margin-bottom: 18px;
            }
            button {
                margin-top: 10px;
            }
            p {
                margin-top: 12px;
            }
        }
        &__hovered {
            .accordion__title {
                padding-bottom: 15px;
            } 
        }
    }
    .vacancies {
        h3 {
            margin-bottom: 5px;
        }
    }
}
